import Vue from "vue";
import Router from "vue-router";
import homepage from "@/components/homepage";
import navbar from "@/components/navbar";

Vue.use(Router);

const router = new Router({
  hashbang: false,
  history: true,
  mode: "history",
  routes: [
    {
      path: "/home",
      name: "Home Page",
      component: homepage
    },
    {
      path: "/*",
      redirect: { name: "Home Page" }
    }
  ]
});

export default router;



